import React, { useEffect, useState } from "react";
import Moment from "react-moment";
import { Link, useParams } from "react-router-dom";
import { Col, Row, Container } from "../components/Grid";
import Jumbotron from "../components/Jumbotron/Jumbotron";
import API from "../utils/API";

function HorseDetail(props) {
  const [horse, setHorse] = useState({})

  const {uniqueName} = useParams()
  useEffect(() => {
    API.getCompetition(uniqueName)
      .then(res => setCompetition(res.data))
      .catch(err => console.log(err));
  }, [])

  return (
    <div>
      <Container fluid>
        <Row>
          <Col size="md-12">
            <Jumbotron>
              <h1>
                Horse: {horse.uniqueName}
                
              </h1>
              <h2>Year of Birth:  {horse.birthYear}</h2>
              <h3>Height: {horse.height}</h3>
              <h4>Breed: {horse.breed}</h4>
              <h4>Colour: {horse.colour}</h4>
           
            </Jumbotron>
          </Col>
        </Row>

        <Row>
        <Col>
            <Link to="/">← Back to Horses</Link>
          </Col>
        </Row>
      </Container>
      </div>
    );
  }


export default HorseDetail;