import React, { useEffect, useState } from "react";
import GridItem from "../components/Grid/GridItem.js";
import GridContainer from "../components/Grid/GridContainer.js";
import { Link, useParams } from "react-router-dom";
import { Col, Row} from "../components/Grid";
import Container from "@material-ui/core/Container";
import Jumbotron from "../components/Jumbotron/Jumbotron";
import API from "../utils/API";

function CompDetail(props) {
  const [competition, setCompetition] = useState({})

  // When this component mounts, grab the competition with the _id of props.match.params.id
  // e.g. localhost:3000/competitions/599dcb67f0f16317844583fc
  const {_id} = useParams()
  useEffect(() => {
    API.getCompetition(_id)
      .then(res => setCompetition(res.data))
      .catch(err => console.log(err));
  }, [])

  return (
  <div>
  <GridContainer>
  <GridItem>
     
 {
  data.map((competition) => (
    <Card className={classes.card}>
              <h1> Event Name: {competition.eventName} with Horse: {competition.horse} </h1>
              <h2>Date: {competition.date}</h2>
              <h3>Placing: {competition.placing}</h3>
              <h4>Penalties: {competition.penalties}</h4>
              <p> Event Type: {competition.eventType}</p>
              <p> Results: {competition.resultNotes} </p>
    </Card>
))
}
  </GridItem>
  </GridContainer>



       </div>
               //     <Link to="/">← Back to Competitions</Link>
    );
  }


export default CompDetail;